import React, { Component } from 'react';
import { BrowserRouter as Router, Route, NavLink } from "react-router-dom";
import axios from 'axios';
import './App.css';
import ComboCreator from './ComboCreator.js';

class User extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userName: '',
            userPassword: '',
            enteredUserName: "",
            enteredUserPassword: "",
            users: [],
            userid: null,
            loginAccepted: false,
            register: false,
            user: [],
            displayedResponse: ''

        }
    }

    // check = (e) => {
    //     e.preventDefault();
    //     this.getRequest();
    //     //let users = this.state.users.map(u => <Users userID={u.userID} userName={u.userName} userPassword={u.userPassword}/>)

    //     for (let i = 0; i < this.state.users.length; i++) {
    //         // console.log("user name"+this.state.users[i].userName+" entered"+this.state.enteredUsername);
    //         if (this.state.users[i].userName === this.state.enteredUserName && this.state.users[i].userPassword === this.state.enteredUserPassword) {
    //             this.setState({ userid: this.state.users[i].userID })
    //             this.setState({ loginAccepted: true });
    //             this.props.setCurrentUser(this.state.userID, this.state.enteredUserName, this.state.loginAccepted);
    //             break;
    //         }

    //     }
    // }

    setCurrentUser = (e) => {
        e.preventDefault();
        this.getRequest();
        // this.setState({loginAccepted:true}); 

        this.setState({ loginAccepted: true, usernameToSend: this.state.user.userName, userIDToSend: this.state.userID });

        console.log(this.state.user.userName);
        console.log(this.state.usernameToSend);

        // this.setState({userIDToSend:this.state.user.userID});
        console.log(this.state.user.userID);
        console.log(this.state.userIDToSend);
        console.log(`user id ${this.state.userIDToSend} username ${this.state.usernameToSend}`);
        this.props.setCurrentUser(this.state.userIDToSend, this.state.usernameToSend, this.state.loginAccepted);

        //console.log(`inside setcurrentuser in login page ${this.state.user.userID} ${this.state.user.userName} ${this.state.loginAccepted}`);
    }

    getRequest = () => {
        axios.get('/getAllUsers').then(r => { this.setState({ users: r.data }) });

    }
    setStates = (event) => {
        this.setState({ [event.target.name]: event.target.value });
    }

    signUp = () => {
        axios.post(`/addUser/${this.state.userName}/${this.state.userPassword}`);
        this.setState({ register: true });
        

    }
    signedUp = (requestResponse, username) => {
        if (requestResponse === "User " + username + " succesfully added.") {
            this.setState({ register: false });
            // this.handleLogin();
        }
        else {
            this.setState({ displayedResponse: requestResponse })
        }

    }

    // pushRequest = () => {

    // }

    setStates = (event) => {
        this.setState({ [event.target.name]: event.target.value });
    }

    handleSubmit = (event) => {
        console.log('A user name was submitted: ' + this.state.userName);
        console.log('A password was submitted: ' + this.state.userPassword);
        event.preventDefault();
    }

    handleLogin = (event) => {
        console.log('A user name was logged: ' + this.state.enteredUserName);
        console.log('A password was logged: ' + this.state.enteredUserPassword);
        event.preventDefault();

    }

    // showThings = () => {
    //     if(this.state.)
    // }

    render() {
        return (
            <div className="body">
                <form onSubmit={this.setCurrentUser} className="form-inline">
                    <form onSubmit={this.handleSubmit} className="form-inline">
                        <div className="container">
                            <div className="row">
                                <div className="col-12">
                                    <label>
                                        <h1>WOMBO COMBO SHOMBO!!!</h1>
                                    </label>
                                </div>
                            </div>
                            <br /><br /><br />
                            <div className="row">
                                <div className="col-6">
                                    <label>
                                        <h4>Sign Up</h4>
                                    </label>
                                </div>
                                <div className="col-6">
                                    <label>
                                        <h4>Log In</h4>
                                    </label>
                                </div>
                            </div>
                            <br />
                            <div className="row">
                                <div className="col-6">
                                    <label>
                                        <input type="text" name='userName' target='one' className="form-control inputArea" onChange={this.setStates} userName={this.state.userName} id="userName" placeholder="Username *"></input>
                                    </label>
                                </div>
                                <div className="col-6">
                                    <label>
                                        <input type="text" name='enteredUserName' target='one' className="form-control inputArea" onChange={this.setStates} enteredUserName={this.state.enteredUserName} id="enteredUserName" placeholder="enteredUsername *"></input>
                                    </label>
                                </div>
                            </div>
                            <br />
                            <div className="row">
                                <div className="col-6">
                                    <label>
                                        <input type="text" name='userPassword' target='one' className="form-control inputArea" onChange={this.setStates} userPassword={this.state.userPassword} id="userPassword" placeholder="Password *"></input>
                                    </label>
                                </div>
                                <div className="col-6">
                                    <label>
                                        <input type="text" name='enteredUserPassword' target='one' className="form-control inputArea" onChange={this.setStates} enteredUserPassword={this.state.enteredUserPassword} id="enteredUserPassword" placeholder="enteredUserPassword *"></input>
                                    </label>
                                </div>
                            </div>
                            <br />
                            <div className="row">
                                <div className="col-6">
                                    <label>
                                        <input type="submit" value="Submit" onClick={this.signUp} />
                                    </label>
                                </div>

                                <div className="col-6">
                                    <label>
                                        <input type="submit" value="Submit" onClick={this.signedUp} />
                                    </label>
                                </div>
                            </div>
                        </div>
                    </form>
                </form>

            </div >
        );
    }
}

export default User;